.header-section {
    
    display: flex;
    justify-content: center;
    align-items: center;
    
    height: 8vh;
    background-color: $main-color;
    clip-path: ellipse(90% 85% at 50% 12%);

    .header-title, i {
        color: $color3;
        font-size: 2.5rem;
        display: inline-block;
        cursor: pointer;
    }

    .header-title { 
        font-family: $theme-font;
    }
    
}